.profile-nav {
  background-color: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  position: relative;
  padding-left: 18px;
  padding-top: 69px;
  padding-bottom: 20px;
  padding-right: 18px;

  @media (min-width: $screen-md) {
    padding-top: 19px;
    border-radius: 10px;
    margin-bottom: 19px;
  }

  &__title {
    font-size: 18px;
    font-weight: bold;
    color: #323232;

    @media (min-width: $screen-md) {
      padding-top: 0;
      font-size: 14px;
    }
  }

  &__list {}

  &__item {
    line-height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__link {
    font-size: 18px;
    line-height: 24px;
    padding-top: 10px;

    @media (min-width: $screen-md) {
      font-size: 14px;
      padding-top: 0;
    }

    &--active {
      color: #f00;

      & + span {
        color: #f00;
      }

      &:hover {
        color: #f00;

        @media (min-width: $screen-md) {
          font-size: 14px;
        }
      }
    }
  }
}
