$rating-star-empty-total-height: 48px;
$rating-star-empty-image: '../img/sprite-008065086354474138.png';
$rating-star-empty: (0px, 0px, 0px, 0px, 22px, 22px, 48px, 48px, '../img/sprite-008065086354474138.png', 'rating__star-empty', );
$rating-star-fill-name: 'rating__star-fill';
$rating-star-fill-x: 26px;
$rating-star-fill-y: 0px;
$rating-star-fill-offset-x: -26px;
