.modal-location {
  z-index: 2000;
  // z-index: 900;
  width: 75%;
  max-width: 300px;

  @media (min-width: $screen-md) {
    width: auto;
    max-width: none;
  }

  .modal-dialog {
    max-width: 768px;
    margin: 0;
    top: 50px;

    @media (min-width: $screen-md) {
      height: 568px;
      margin: 1.75rem auto;
      top: 0;
    }

    &.large {
      @media (min-width: $screen-md) {
        max-width: 997px;
      }
    }
    &.medium {
      @media (min-width: $screen-md) {
        max-width: 900px;
      }
    }
    &.small {
      @media (min-width: $screen-md) {
        max-width: 768px;
      }
    }
  }

  .searchHolder {
    width: 100%;
    display: inline;
    overflow: visible;
    position: relative;
  }

  .autoFillBar {
    background: #fff;
    width: 100%;
    height: 60px;
    position: absolute;
    top: 40px;
    z-index: 9;
    overflow: auto;
  }

  .autoFillBar .item {
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 18px;
    padding-right: 18px;
  }

  &__content {
    border-radius: 0;
    border: none;

    @media (min-width: $screen-md) {
      border-radius: 6px;
      border: 1px solid rgba(0,0,0,.2);
    }
  }

  &__header {
    display: block;
    text-align: center;
    position: relative;
    padding: 17px;
    padding-bottom: 53px;
    background-color: #fff;
    border-bottom: 1px solid #d8d8d8;

    @media (min-width: $screen-md) {
      border-radius: 6px 6px 0 0;
      padding-bottom: 17px;
    }
  }

  &__title {
    color: #323232;
    font-weight: bold;
    display: inline-block;
    font-size: 14px;

    @media (min-width: $screen-md) {
      font-size: 20px;
    }
  }

  &__close {
    display: none;
    position: absolute;
    top: 20px;
    right: 20px;

    @media (min-width: $screen-md) {
      display: inline-block;
      width: 27px;
      height: 27px;
      min-height: 0;
      top: 0;
      right: 0;
      background-color: #fff;
      border-radius: 50%;
      border: 1px solid #f0f0f0;
      padding: 4px !important;
      margin: -1rem -1rem!important;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    }
  }

  &__close-icon {
    width: 14px;
    height: 14px;
  }

  &__body {
    background-color: #fff;
    overflow-y: auto;
    padding: 0;

    @media (min-width: $screen-md) {
      padding-top: 40px;
      padding-left: 45px;
      padding-right: 45px;
      padding-bottom: 20px;
    }
  }

  &__city-char-list {

    @media (min-width: $screen-md) {
      column-count: 4;
      column-gap: 3px;
    }
  }

  &__city-char-item {
    display: flex;
    position: relative;
  }

  &__city-char {
    display: inline;
    font-size: 14px;
    color: #d1d1d1;
    font-weight: bold;
    line-height: 30px;
    padding-left: 16px;
    position: absolute;
    left: 0;

    @media (min-width: $screen-md) {
      position: static;
    }
  }

  &__city-list {
    vertical-align: top;
    width: 100%;

    @media (min-width: $screen-md) {
      padding-left: 14px;
    }
  }

  &__city-item {
    // line-height: 34px;
    border-bottom: 1px solid #d8d8d8;

    @media (min-width: $screen-md) {
      border: none;
      padding-left: 16px;
      padding-right: 16px;
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }

  &__city-link {
    font-size: 14px;
    font-weight: bold;
    color: #969696;
    line-height: 30px;
    padding-left: 40px;
    padding-right: 16px;
    display: block;

    @media (min-width: $screen-md) {
      line-height: 1;
      border: none;
      padding: 0;
    }

    &:hover {
      font-weight: bold;
      color: #969696;
    }
  }

  &__footer {
    justify-content: center;
    padding: 0;
    background-color: #fff;

    @media (min-width: $screen-md) {
      padding-top: 23px;
      padding-bottom: 27px;
      border-radius: 0 0 6px 6px;
    }
  }

  &__search {
    position: absolute;
    top: 43px;
    width: 100%;
    padding-left: 17px;
    padding-right: 17px;
    display: flex;

    @media (min-width: $screen-md) {
      position: static;
      width: auto;
      display: block;
    }
  }

  &__label {
    font-weight: bold;
    font-size: 14px;
    color: #323232;
    display: none;
    padding-bottom: 10px;

    @media (min-width: $screen-md) {
      display: block;
    }
  }

  &__input {
    font-size: 14px;
    height: 32px;
    line-height: 32px;
    color: #969696;
    background-color: #eeeeee;
    border: 1px solid #dedede;
    border-radius: 4px;
    padding: 6px 15px 8px 15px;
    width: 100%;

    &:focus {
      background-color: #fff;
    }

    @media (min-width: $screen-md) {
      width: 462px;
    }
  }

  &__btn {
    display: inline-block;
    border-radius: 4px;
    line-height: 17px;
    height: 32px;
    font-weight: normal;
    color: #787878;
    border: 1px solid #dedede;
    background-color: #ffffff;

    &:hover {
      color: #787878;
      font-weight: normal;
      background-color: #eeeeee;
    }

    &:active {
      color: #787878;
      font-weight: normal;
      background-color: #dcdcdc;
    }

    @media (min-width: $screen-md) {
      margin-left: 15px;
    }
  }
}
